.dark-mode {
  background-color: #1a1a1a; }
  .dark-mode p {
    color: #FFFFFF; }
  .dark-mode a,
  .dark-mode h1,
  .dark-mode h2,
  .dark-mode h3,
  .dark-mode h4 {
    color: #e1e1e1; }
  .dark-mode .anuncio,
  .dark-mode .resumen-propiedad {
    background-color: #333333;
    border: 1px solid #333333; }
    .dark-mode .anuncio .precio,
    .dark-mode .resumen-propiedad .precio {
      color: #71B100; }
    .dark-mode .anuncio .icono,
    .dark-mode .resumen-propiedad .icono {
      filter: invert(100%); }
  .dark-mode .texto-nosotros blockquote {
    color: #FFFFFF; }
  .dark-mode .formulario label,
  .dark-mode .formulario legend,
  .dark-mode .formulario p {
    color: #e1e1e1; }
  .dark-mode .formulario input:not([type="submit"]),
  .dark-mode .formulario textarea,
  .dark-mode .formulario select {
    background-color: #333333;
    border: 1px solid #333333;
    color: #FFFFFF; }

/** UI **/
.no-webp .inicio {
  background-image: url(../img/header.jpg); }

.webp .inicio {
  background-image: url(../img/header.webp); }

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0; }
  .header.inicio {
    background-position: center center;
    background-size: cover; }
    @media (min-width: 768px) {
      .header.inicio {
        min-height: 70rem; } }
    @media (min-width: 768px) {
      .header.inicio .contenido-header {
        min-height: 70rem; } }

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .contenido-header h1 {
    text-align: left;
    color: #FFFFFF;
    font-weight: 700;
    line-height: 2;
    max-width: 60rem; }

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0; }
  .footer .contenedor-footer {
    padding-top: 2.5rem; }
  .footer .navegacion {
    visibility: visible;
    opacity: 1;
    height: auto; }
  .footer .copyright {
    color: #FFFFFF;
    text-align: center; }

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media (min-width: 768px) {
    .derecha {
      align-items: flex-end; } }
  .derecha .dark-mode-boton {
    width: 3rem;
    margin-top: 3rem;
    margin-bottom: 2rem;
    filter: invert(100%); }
    .derecha .dark-mode-boton:hover {
      cursor: pointer; }
    @media (min-width: 768px) {
      .derecha .dark-mode-boton {
        margin-top: 0; } }

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem; }
  @media (min-width: 768px) {
    .barra {
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-end; } }

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear; }
  @media (min-width: 768px) {
    .navegacion {
      display: flex;
      align-items: center;
      opacity: 1;
      visibility: visible; } }
  .navegacion a {
    color: #FFFFFF;
    text-decoration: none;
    font-size: 2.2rem;
    display: block;
    text-align: center; }
    @media (min-width: 768px) {
      .navegacion a {
        margin-right: 2rem;
        font-size: 1.8rem; }
        .navegacion a:last-of-type {
          margin-right: 0; } }
    .navegacion a:hover {
      color: #71B100; }

/** Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none; } }

.mobile-menu img {
  width: 5rem; }

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto; }

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr); } }

.icono {
  text-align: center; }
  .icono img {
    height: 10rem; }
  .icono h3 {
    text-transform: uppercase; }

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    grid-template-columns: minmax(0, auto) minmax(0, auto) minmax(0, auto);
    grid-template-rows: minmax(0, auto);
    gap: 1rem; } }

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem; }
  .anuncio .contenido-anuncio {
    padding: 2rem; }
  .anuncio h3,
  .anuncio p {
    margin: 0;
    word-wrap: break-word; }

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem; }

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem; }
  .iconos-caracteristicas li {
    flex: 1;
    display: flex; }
    .iconos-caracteristicas li img {
      flex: 0 0 3rem;
      margin-right: 1rem; }
    .iconos-caracteristicas li p {
      font-weight: 900; }

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg"); }

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp"); }

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center; }
